.production__detail {
    width: 100%;
    height: auto;
   
    .production__title {
        font-size: 20px;
        font-weight: bold;
        color: $color9;
        line-height: 26px;
    }
    
}
.production__rate {
    width: 100%;
    height: auto;
    display: flex;
    flex-wrap: nowrap;
    p {
        display: inline-block;
    }
    .star__avg {
        font-size: 20px;
        color: $color3;
        font-family: $roboto;
        font-weight: 400;
    }
    .star__rate {
        font-size: 18px;
        font-family: $roboto;
        font-weight: 600;
        color: $color3;
        margin-left: 10px;
        margin-right: 10px;
    }
    .rate__count {
        margin-left: 10px;
        margin-right: 10px;
        
        .text {
            font-size: 18px;
            font-family: inherit;
            font-weight: 400;
            line-height: 26px;
            color: $color7;
        }
    }
    .sale__quantity {
        margin-left: 10px;
        .text {
            font-size: 18px;
            line-height: 26px;
            font-family:inherit;
            font-weight: 400;
            color: $color7;
        }
    }
}

.info {
    .live p {
        text-align: left;
    }
    .hand {
        float: none;
        width: 100%;
    }
    .transport-text {
        font-size: 15px ;
        font-weight: bold;
        font-family: inherit;
        span {
            color: $color3;
            font-weight: normal;
        }
    }
}